<template>
  <div>


<nav class="navbar navbar-expand-md navbar-dark bg-dark fixed-top">
  <span class="navbar-brand">履歷 Resume</span>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#myNavbar" aria-controls="myNavbar" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="collapse navbar-collapse" id="myNavbar">
    <ul class="navbar-nav mr-auto">
      <li class="nav-item">
        <a class="nav-link" @click.prevent="handler">基本資料</a>
      </li>
      <li class="nav-item">
        <a class="nav-link" @click.prevent="moveTo('#abilityArea')">相關技能</a>
      </li>
      <li class="nav-item">
        <a class="nav-link" @click.prevent="moveTo('#experienceArea')">工作經歷</a>
      </li>
      <li class="nav-item dropdown">
        <a class="nav-link dropdown-toggle" href="#" id="myWork" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">我的作品</a>
        <div class="dropdown-menu" aria-labelledby="myWork">
          <small class="dropdown-header">Github連結</small>
          <div v-for="item in myWork" :key="item.id">
            <a class="dropdown-item" :href="item.path" target="_blank">{{item.name}}</a>
          </div>
          <div class="dropdown-divider"></div>
          <small class="dropdown-header">照片示範</small>
          <div v-for="item in myAblum" :key="item.id">
            <router-link class="dropdown-item" :to="item.path">{{item.name}}</router-link>
          </div>
        </div>
      </li>
    </ul>
  </div>
</nav>

  </div>
</template>

<script>
import $ from 'jquery';
export default {
  data(){
    return{
      myWork:[
        {name:'代辦清單', path:'https://johnsmith-wang.github.io/vuexTodo', id:'git01'},
        {name:'BMI計算機', path:'https://johnsmith-wang.github.io/bmicalc', id:'git02'},
        {name:'高雄景點查詢網', path:'https://johnsmith-wang.github.io/touristweb', id:'git03'},
        {name:'AQI品質監測', path:'https://github.com/JohnSmith-Wang/05.AQI_Website', id:'git04'}
      ],
      myAblum:[
        {name:'口罩地圖', path:'/maskmap', id:'photo01'},
        {name:'簡易電商網站', path:'/vuestore', id:'photo02'}
      ]
    }
  },
  methods:{
    handler(){
      if(this.$route.name !== 'Home'){
        this.$router.push('/')
      }else{
        this.moveTo('body')
      }
    },
    moveTo(id){
      $('html, body').animate({scrollTop: $(id).position().top}, 1)
    }
  }
}
</script>

<style lang="scss" scoped>
a{
  cursor: pointer;
}
</style>