<template>
  <div>
    
    <div class="container justify-content-center">
      <div class="row">
        <div class="col-12 col-xl-6" v-for="(item,index) in info" :key="index">
          <div class="card bg-gray">
            <img class="card-img-top" :src="path+item.page"  alt="Card image cap" data-toggle="modal" data-target="#zoomImgModal" @click.prevent="zoom(item)">
            <div class="card-body">
              <p class="card-text">{{item.text}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>

<!-- Modal -->
     <div class="modal fade bd-example-modal-xl" id="zoomImgModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
       <div class="modal-dialog modal-xl" role="document">
         <div class="modal-content">
            <div class="modal-boyd">
              <img id="zoomImg" class="img-fluid" :src="tempimg"  alt="Card image cap">
            </div>
          </div>
        </div>
      </div>

  </div>
</template>

<script>
export default {
  data(){
    return{
      tempimg:'./img/mywork/maskmap/01.png',
    }
  },
  props:{
    info:{
      type:Array,
      require:true
    },
    path:{
      type:String,
      require:true
    }
  },
  methods:{
    zoom(data){
      this.tempimg = this.path+data.page;
    }
  }
}
</script>

<style lang="scss" scoped>

.card{
margin: 10px auto;
}

.bg-gray{
  background-color: rgb(158, 154, 154);
  color: rgb(255, 255, 255);
  font-size: 17px;
  line-height: 30px;
}

.card-img-top{
  cursor: pointer;
}
</style>