import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Maskmap from '../views/ablumPage/Maskmap.vue'
import Vuestore from '../views/ablumPage/Vuestore.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path:'/maskmap',
    name: 'Maskmap',
    component: Maskmap
  },
  {
    path:'/vuestore',
    name: 'Vuestore',
    component: Vuestore
  },
]

const router = new VueRouter({
  routes,
  linkActiveClass: 'active'
})

export default router
